.main{
    background: url(https://img.freepik.com/premium-vector/pi-day-seamless-pattern-with-mathematical-constants-baked-pie-template-hand-drawn-illustration_2175-12164.jpg?w=740);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 100vh;
}

h1{
    font-size: xx-large;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
.calculator-body{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.calculator{
    border: 2px  solid rgb(0, 0, 0);
    border-radius: 20px;
    background: rgba(128, 128, 128, 0.496);
}

button{
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: rgb(255, 255, 255);
    margin: 2px;
    font-size: larger;
}

.orange{
    background-color: rgb(255, 136, 0);
}

.gray{
    background-color: rgb(212, 212, 212);
}

.result{
    height: 30px;
    text-align: end;
    width: 250px;
    font-size: xx-large;
    
}